@import url("https://fonts.googleapis.com/css2?family=Cairo:wght@200;300;400;500;600;700;800;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "cairo", sans-serif;
}

html {
  scroll-behavior: smooth;
}
li {
  list-style: none;
}
a {
  color: #000;
  text-decoration: none;
}
